import './style.css';
import * as THREE from 'three';
//import * as ARjs from 'ar.js/three.js/build'
import { ARButton } from '/src/ARButton.js';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { AnimationMixer } from 'three';
import { MathUtils } from 'three';

// global variables
let camera, scene, renderer;
let controller;
let rotationSpeed = [];
let scaleFragment = [];
let directionX = [], directionY = [], directionZ = [];
let meshBaseLine = null;
let scale = 0;

// texture loader
const texturelLoader = new THREE.TextureLoader();

// animation
let mixerBase;

let loadedfbx = false;
function init() {

    // scenario build
	const container = document.createElement('div');
	document.body.appendChild(container);
	scene = new THREE.Scene();
	camera = new THREE.PerspectiveCamera(70, window.innerWidth / window.innerHeight, 0.01, 100);
    const light = new THREE.HemisphereLight(0xffffff, 0xbbbbff, 1.25);
	//light.position.set(0.5, 3, 0.25);
	scene.add(light);
	renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
	renderer.setPixelRatio(window.devicePixelRatio);
	renderer.setSize(window.innerWidth, window.innerHeight);
	renderer.xr.enabled = true;
	container.appendChild(renderer.domElement);
	document.body.appendChild(ARButton.createButton(renderer));

    // gltf loader
	const gltfLoader = new GLTFLoader();
    const fbxLoader = new FBXLoader();
    
    // CONTROLLERS FOR SCALE AND POSITION
    let masterScale = 0.1;
    let masterPos = [0, -.15, -.5];

    // LOADING BASELINE, MATERIAL AND ROTATION
    fbxLoader.load(
        '/MESH_04.fbx',
        (fbx) => {
            const model = fbx;
            console.log(model);
            meshBaseLine = model.children[0].children[0];
            console.log(meshBaseLine);
            meshBaseLine.scale.set(masterScale, masterScale, masterScale);
            console.log(meshBaseLine.length);
            for (let j = 0; j< meshBaseLine.children.length; j++) {
                    
                    scaleFragment.push(meshBaseLine.children[j].scale);
                    directionX.push(-1), directionY.push(-1), directionZ.push(-1);
                    meshBaseLine.children[j].material = meshBaseLine.children[j].material.clone();
                    meshBaseLine.children[j].material.metalness = 0;
                    meshBaseLine.children[j].material.premultipliedAlpha = true;
                    //meshBaseLine.children[j].material.blending = THREE.AdditiveBlending;
                    //meshBaseLine.children[j].material.transparent = true;
                    meshBaseLine.children[j].material.alphaTest = 0.5;
                    //meshBaseLine.children[j].material.wireframe = true;
                    //meshBaseLine.children[j].material.wireframeLinecap ="square";
                    //meshBaseLine.children[j].material.wireframeLinewidth = 1;
                    //meshBaseLine.children[j].material.color.setHex(0x101010);
                    meshBaseLine.children[j].material.side = THREE.DoubleSide;
                
            }
            
            console.log(rotationSpeed.length);
            //console.log(scaleFragment);
            meshBaseLine.position.set(masterPos[0],masterPos[1],masterPos[2]).applyMatrix4(controller.matrixWorld);
            //meshBaseLine.quaternion.setFromRotationMatrix(controller.matrixWorld);
            meshBaseLine.rotation.x = -Math.PI;
            meshBaseLine.rotation.z = Math.PI;
            console.log("adding mesh");
            scene.add(meshBaseLine);
            console.log("mesh loaded");
            loadedfbx = true;
		}
    );




    // CONTROL SPEED OF ANIMATION
    function Speed() {
        return Math.random(0, 10) * 0.01;
        //return 0.001;
    }

    // Detects finger on the screen
    function onSelect() {
        //place the objects
        meshBaseLine.position.set(masterPos[0],masterPos[1],masterPos[2]).applyMatrix4(controller.matrixWorld);
        //meshBaseLine.quaternion.setFromRotationMatrix(controller.matrixWorld);
        meshBaseLine.rotation.x = -Math.PI;
        meshBaseLine.rotation.z = Math.PI;
        scene.add(meshBaseLine);
	}

    // final setup
	controller = renderer.xr.getController(0);
    controller.addEventListener('select', onSelect);
	scene.add(controller);
	window.addEventListener('resize', onWindowResize);
    console.log("done init");
}

function handleScale() {
    this.scaleFactor *= 1 + event.detail.spreadChange / event.detail.startSpread;

    this.scaleFactor = Math.min(
        Math.max(this.scaleFactor, this.data.minScale),
        this.data.maxScale
    );
    let Scalefactor = [this.scaleFactor, this.scaleFactor, this.scaleFactor,];
    let initScaleBase = meshBaseLine.scale;
    meshBaseLine.scale = initScaleBase + Scalefactor;
}

function onWindowResize() {
	camera.aspect = window.innerWidth / window.innerHeight;
	camera.updateProjectionMatrix();
	renderer.setSize(window.innerWidth, window.innerHeight);
}

// render every frame
function animate() {
	renderer.setAnimationLoop(render);
}

// render function
function render() {
	renderer.render(scene, camera);
}

// random rotation to every bone
function animation() {
    if (!loadedfbx) return;
    if (meshBaseLine != null) {
        meshBaseLine.rotation.y += 0.002;
//        for (let i = 0; i < meshBaseLine.children.length; i++) {
//            meshBaseLine.children[i].rotation.y += rotationSpeed[i];
//        }
    }
}

function addGlitch() {
    if (!loadedfbx) return;
    if (meshBaseLine != null) {
        for (let j = 0; j< meshBaseLine.children.length; j++) {
            
                let c = new THREE.Color( 'white' );//new THREE.Color( Math.random() * 0xffffff );//
                if (Math.random(0, 1) > 0.5) {
                    meshBaseLine.children[j].material.wireframe = true;
                    c = new THREE.Color( 'black' );
                }
                else {
                    meshBaseLine.children[j].material.wireframe = false;
                }
                meshBaseLine.children[j].material.color = c;
          
        }
    }
}

// creates a random rotation increment
function randomRotationIncrement() {
    let direction = (Math.random(0, 1) < 0.5) ? 1 : -1;
    return Math.random(0, 2) * 0.001 * direction;
}


// Time
const clock = new THREE.Clock()
var startFrame = true;
let previousTime = 0;
let timeToUpdate = 1;
let timeToGlitch = 0;
const tick = () => {
	//get time
	const elapsedTime = clock.getElapsedTime();
	const deltaTime = elapsedTime - previousTime;
	previousTime = elapsedTime;

    // animates the rotation of the bones
    //animation();
    
    let sinewave = MathUtils.inverseLerp(-1,1,Math.sin(elapsedTime*0.1));
    
    if (meshBaseLine != null) {
        //meshBaseLine.rotation.y = MathUtils.lerp(0,2*Math.PI,sinewave);
        meshBaseLine.rotation.y += 0.0006;
    }
    
    if (elapsedTime - timeToGlitch > MathUtils.lerp(0.1,0.5,sinewave)) {
        timeToGlitch = elapsedTime;
        addGlitch();
    }

    // update all animations
    if (mixerBase != null) {
        mixerBase.update(deltaTime);
    }

	// Call tick again on the next frame
	window.requestAnimationFrame(tick)
	if (startFrame) {
		startFrame = false;
	}
}


init();
animate();
tick();
